import React, { useState } from "react";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  NavbarText,
} from "reactstrap";
import { Link } from "react-router-dom";
import Ingredients from "../navi/Ingredients";

const Navi = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div>
      <Navbar className="navbar navbar-dark bg-dark mb-1" light expand="md">
        <Link to="/">
          <NavbarBrand>
            <h3>Cocktails Store</h3>
          </NavbarBrand>
        </Link>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="mr-auto" navbar>
            <NavItem>
              <Link to={"/RandomCocktail"}>
                <NavLink>Random Cocktail</NavLink>
              </Link>
            </NavItem>
            <NavItem>
              <Link to={"/CocktailDatabase"}>
                <NavLink>Most Latest Cocktails</NavLink>
              </Link>
            </NavItem>
            <Ingredients></Ingredients>
          </Nav>
          <NavbarText>Â©Cocktail DB</NavbarText>
        </Collapse>
      </Navbar>
    </div>
  );
};

export default Navi;
